#container {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}

.video {
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%)
}

.border {
  z-index: 2;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 300px;
  height: 225px;
  margin: auto;
  border: 5px green solid;
}

.result {
  position: fixed;
  bottom: 200px;
  left: 50px;
  color: white;
}

.button {
 position: fixed;
  top: 0;
  left: 0;
  width: 50px;
  height: 50px;
  color: red;
}
